import $ from "jquery";
import React, { Component } from "react";
import "./wordsearch.css";

import {
  Button,
  ButtonGroup,
  ButtonToolbar,
  Col,
  Container,
  Dropdown,
  Row,
} from "react-bootstrap";
import ScrabbleService from "../services/scrabble";
import { allOrientations, orientations } from "./data";
import { WordFind } from "./WF";
export class WordSearchComponent extends Component<
  {},
  {
    matrix: any;
    combine: any;
    levels: any;
  }
> {
  level = "Level 1";
  levelId = 1;
  levels = [];
  width = 10;
  height = 10;
  wordList: any = [];
  wordListHindi: any = [];
  startSquare: any;
  selectedSquares: any = [];
  curWord: any;
  curOrientation: any;
  gl_id = 0;

  constructor(props: any) {
    super(props);
  }
  async componentDidMount() {
    await ScrabbleService.getWordSearchLevelsByGameId().then((levels) => {
      console.log("then");
      console.log(levels);
      this.setState({ levels: levels });
      ScrabbleService.getWordSearchDataByLevel(1).then((data) => {
        this.wordList = data.word_lists.map((d: any) => {
          const word = d.eword.toUpperCase();
          return word;
        });
        this.wordListHindi = data.word_lists.map((d: any) => d.hword);
        this.reCreate();
        this.createBindings();
      });
    });
  }
  componentDidUpdate() {
    this.createBindings();
  }
  createBindings() {
    if (window.navigator.msPointerEnabled) {
      $(".puzzleSquare").on("MSPointerDown", this.startTurn.bind(this));
      $(".puzzleSquare").on("MSPointerOver", this.select.bind(this));
      $(".puzzleSquare").on("MSPointerUp", this.endTurn.bind(this));
    } else {
      $(".puzzleSquare").mousedown(this.startTurn.bind(this));
      $(".puzzleSquare").mouseenter(this.mouseMove.bind(this));
      $(".puzzleSquare").mouseup(this.endTurn.bind(this));
      $(".puzzleSquare").on("touchstart", this.startTurn.bind(this));
      $(".puzzleSquare").on("touchmove", this.touchMove.bind(this));
      $(".puzzleSquare").on("touchend", this.endTurn.bind(this));
    }
  }
  select(target: any) {
    // if the user hasn't started a word yet, just return
    if (!this.startSquare) {
      return;
    }
    // if the new square is actually the previous square, just return
    var lastSquare = this.selectedSquares[this.selectedSquares.length - 1];
    if (lastSquare === target) {
      return;
    }

    // see if the user backed up and correct the selectedSquares state if
    // they did
    let backTo;
    for (var i = 0, len = this.selectedSquares.length; i < len; i++) {
      if (this.selectedSquares[i] === target) {
        backTo = i + 1;
        break;
      }
    }

    while (backTo && backTo < this.selectedSquares.length) {
      $(this.selectedSquares[this.selectedSquares.length - 1]).removeClass(
        "selected"
      );
      this.selectedSquares.splice(backTo, 1);
      this.curWord = this.curWord.substr(0, this.curWord.length - 1);
    }
    const x1: any = this.startSquare.attributes["x"].value - 0;
    const x2: any = this.startSquare.attributes["y"].value - 0;
    const x3: any = target.currentTarget
      ? target.currentTarget.attributes["x"].value - 0
      : target.attributes["x"].value - 0;
    const x4: any = target.currentTarget
      ? target.currentTarget.attributes["y"].value - 0
      : target.attributes["y"].value - 0;
    const newOrientation = this.calcOrientation(x1, x2, x3, x4);

    if (newOrientation) {
      this.selectedSquares = [this.startSquare];
      this.curWord = $(this.startSquare).text();
      if (lastSquare !== this.startSquare) {
        $(lastSquare).removeClass("selected");
        lastSquare = this.startSquare;
      }
      this.curOrientation = newOrientation;
    }

    const x11: any = lastSquare.attributes["x"].value - 0;
    const x21: any = lastSquare.attributes["y"].value - 0;
    const x31: any = target.currentTarget
      ? target.currentTarget.attributes["x"].value - 0
      : target.attributes["x"].value - 0;
    const x41: any = target.currentTarget
      ? target.currentTarget.attributes["y"].value - 0
      : target.attributes["y"].value - 0;
    // see if the move is along the same orientation as the last move
    var orientation = this.calcOrientation(x11, x21, x31, x41);

    // if the new square isn't along a valid orientation, just ignore it.
    // this makes selecting diagonal words less frustrating
    if (!orientation) {
      return;
    }

    // finally, if there was no previous orientation or this move is along
    // the same orientation as the last move then play the move
    if (!this.curOrientation || this.curOrientation === orientation) {
      this.curOrientation = orientation;
      this.playTurn(target.currentTarget ? target.currentTarget : target);
    }
  }
  mouseMove(event: any) {
    this.select(event);
  }
  startTurn(event: any) {
    $(event.target).addClass("selected");
    this.startSquare = event.target;
    this.selectedSquares.push(event.target);
    this.curWord = event.target.textContent;
  }
  calcOrientation(x1: any, y1: any, x2: any, y2: any) {
    for (var orientation in orientations) {
      var nextFn = orientations[orientation];
      var nextPos = nextFn(x1, y1, 1);

      if (nextPos.x === x2 && nextPos.y === y2) {
        return orientation;
      }
    }

    return null;
  }
  playTurn(square: any) {
    console.log("playTurn");
    // make sure we are still forming a valid word
    for (var i = 0, len = this.wordList.length; i < len; i++) {
      if (this.wordList[i].indexOf(this.curWord + square.textContent) === 0) {
        square.classList.add("selected");
        this.selectedSquares.push(square);
        this.curWord += square.textContent;
        break;
      }
    }
  }
  touchMove(e: any) {
    var xPos = e.originalEvent.touches[0].pageX;
    var yPos = e.originalEvent.touches[0].pageY;
    var targetElement: any = document.elementFromPoint(xPos, yPos);
    if (targetElement?.classList.contains("puzzleSquare"))
      this.select(targetElement);
  }
  endTurn() {
    // see if we formed a valid word
    for (var i = 0, len = this.wordList.length; i < len; i++) {
      if (
        this.wordList[i] === this.curWord &&
        !this.state.combine
          .filter((w: any) => {
            return w.selected === true;
          })
          .map((e: any) => e.word)
          .includes(this.curWord)
      ) {
        $(".selected").addClass("found");
        const classToApply =
          "found" +
          this.state.combine.filter((w: any) => {
            return w.selected === false;
          }).length;
        // $(".selected").addClass(classToApply);
        $(".selected").attr(
          "class",
          classToApply + " " + $(".selected").attr("class")
        );
        this.reveal(this.wordList.indexOf(this.curWord), classToApply);
        $('input.word[value="' + this.curWord + '"]').addClass("wordFound");
        if (
          this.state.combine.filter((w: any) => {
            return w.selected === false;
          }).length === 0
        ) {
          this.gameComplete();
        }
      }
    }
    // reset the turn
    $(".selected").removeClass("selected");
    this.startSquare = null;
    this.selectedSquares = [];
    this.curWord = "";
    this.curOrientation = null;
  }
  createMatrix() {
    var options = {
      height: this.height,
      width: this.width,
      orientations: allOrientations,
      preferOverlap: true,
    };
    const WF: any = WordFind();
    const fill = WF.newPuzzle(this.wordList, options);
    let matrix: any = [];
    for (let i = 0; i < this.width; i++) {
      matrix[i] = [];
      for (let j = 0; j < this.height; j++) {
        matrix[i][j] = { x: i, y: j, selected: false, letter: fill[i][j] };
      }
    }
    return matrix;
  }
  async reCreate() {
    let data = await ScrabbleService.getWordSearchDataByLevel(this.levelId);
    this.wordList = data.word_lists.map((d: any) => d.eword.toUpperCase());
    this.wordListHindi = data.word_lists.map((d: any) => d.hword);
    const matrix = this.createMatrix();
    const combine: any = [];
    this.wordList.forEach((word: string, index: number) => {
      combine.push({
        word: word,
        wordHindi: this.wordListHindi[index],
        selected: false,
      });
    });
    let elements: any = document.getElementsByClassName("found");
    if (elements.length > -1)
      [...elements].map((el: any) => {
        return el.classList.remove("found");
      });
    elements = document.getElementsByClassName("selected");
    if (elements.length > -1)
      [...elements].map((el: any) => {
        return el.classList.remove("selected");
      });
    for (let i = 1; i < 12; i++) {
      elements = document.getElementsByClassName("found" + i);
      if (elements.length > -1)
        [...elements].map((el: any) => {
          return el.classList.remove("found" + i);
        });
    }
    this.setState({ matrix, combine });
  }
  reveal(index: number, classToApply: string) {
    const combine = this.state.combine;
    combine[index].selected = true;
    combine[index].classToApply = classToApply;
    this.setState({ combine });
  }
  gameComplete() {
    alert("Game Complete");
  }
  async changeLevel(level: any) {
    this.level = "Level " + level;
    this.levelId = level;
    let data = await ScrabbleService.getWordSearchDataByLevel(level);
    this.wordList = data.word_lists.map((d: any) => d.eword);
    this.wordListHindi = data.word_lists.map((d: any) => d.hword);
    if (this.level === "Level 1") {
      this.height = 10;
      this.width = 10;
    } else if (this.level === "Level 2") {
      this.height = 10;
      this.width = 10;
    } else {
      this.height = 10;
      this.width = 10;
    }
    this.reCreate();
    this.setState({});
  }
  render() {
    const show =
      this.state?.levels?.length > 0 && this.state.matrix !== undefined;
    console.log(show);
    console.log(this.state?.matrix);
    return (
      <>
        <br></br>
        {show && (
          <>
            <Row
              className="justify-content-center text-center"
              style={{ marginBottom: "15px", textAlign: "center" }}
            >
              <ButtonToolbar aria-label="Toolbar with button groups">
                <ButtonGroup className="mr-2" aria-label="First group1">
                  <Dropdown>
                    <Dropdown.Toggle variant="success" id="dropdown-basic">
                      {this.level}
                    </Dropdown.Toggle>

                    <Dropdown.Menu>
                      {this.state?.levels.map((level: any) => (
                        <Dropdown.Item
                          key={level.gl_id}
                          onSelect={this.changeLevel.bind(this, level.gl_id)}
                        >
                          {level.level_name}
                        </Dropdown.Item>
                      ))}
                    </Dropdown.Menu>
                  </Dropdown>
                </ButtonGroup>
                <ButtonGroup className="mr-2" aria-label="First group">
                  <Button
                    variant="outline-dark"
                    onClick={this.reCreate.bind(this)}
                  >
                    New Game
                  </Button>{" "}
                </ButtonGroup>
              </ButtonToolbar>
            </Row>{" "}
            <Row className="justify-content-center text-center word-search">
              <div>
                <table>
                  <tbody>
                    {this.state.matrix.map((i: any, index: any) => (
                      <tr key={index}>
                        {i.map((j: any, index2: any) => {
                          const props: any = {
                            x: j.x,
                            y: j.y,
                          };
                          return (
                            <td key={index2} className="td">
                              <div
                                key={j.x + "" + j.y}
                                {...props}
                                className={"puzzleSquare"}
                                style={{
                                  width: "40px",
                                  height: "40px",
                                  lineHeight: "40px",
                                }}
                              >
                                {j.letter?.toUpperCase()}
                              </div>
                            </td>
                          );
                        })}
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </Row>
            <Row
              className="justify-content-center text-center"
              style={{ marginTop: "15px" }}
            >
              <Col>
                --------------------------- Find These words
                ---------------------------
              </Col>
            </Row>{" "}
            <Row className="justify-content-center text-center">
              {this.wordList.map((word: string, index: any) => {
                return (
                  <>
                    <ButtonGroup className="mr-2" aria-label="Second group">
                      <Button
                        key={index}
                        className={
                          "btn-disabled " +
                          this.state.combine[index].classToApply
                        }
                        variant={" outline-secondary"}
                        style={{ width: "100px", marginTop: "15px" }}
                      >
                        {this.state.combine[index].selected
                          ? this.state.combine[index].wordHindi
                          : this.state.combine[index].word}
                      </Button>
                    </ButtonGroup>
                  </>
                );
              })}
            </Row>
          </>
        )}
      </>
    );
  }
}
