import React, { Component } from 'react';
import Crossword from '@jaredreisinger/react-crossword';
import { Button, ButtonGroup, ButtonToolbar, Dropdown } from 'react-bootstrap';
// const clue = '';
const data = {
  across: {
    1: { clue: 'This', answer: 'XXX', row: 0, col: 0 },
    4: { clue: 'is', answer: 'XXX', row: 0, col: 4 },
    7: { clue: 'not', answer: 'XXX', row: 1, col: 0 },
    8: { clue: 'a', answer: 'XXXX', row: 1, col: 4 },
    10: { clue: 'real', answer: 'XX', row: 2, col: 0 },
  },
  down: {
    1: { clue: 'create.', answer: 'XXXX', row: 0, col: 0 },
    2: { clue: 'All', answer: 'XXXX', row: 0, col: 1 },
    3: { clue: 'of', answer: 'XX', row: 0, col: 2 },
    4: { clue: 'the', answer: 'XXXXXX', row: 0, col: 4 },
    5: { clue: 'answers', answer: 'XX', row: 0, col: 5 },
  },
};
export class CrossWord extends Component {
  // data = {
  //   across: {
  //   },
  //   down: {
  //   },
  // };
  componentDidMount() {
    this.setState({
      gameData: {
        across: {
          1: { clue: 'This', answer: 'XXX', row: 0, col: 0 },
          4: { clue: 'is', answer: 'XXX', row: 0, col: 4 },
          7: { clue: 'not', answer: 'XXX', row: 1, col: 0 },
          8: { clue: 'a', answer: 'XXXX', row: 1, col: 4 },
          10: { clue: 'real', answer: 'XX', row: 2, col: 0 },
        },
        down: {
          1: { clue: 'create.', answer: 'XXXX', row: 0, col: 0 },
          2: { clue: 'All', answer: 'XXXX', row: 0, col: 1 },
          3: { clue: 'of', answer: 'XX', row: 0, col: 2 },
          4: { clue: 'the', answer: 'XXXXXX', row: 0, col: 4 },
          5: { clue: 'answers', answer: 'XX', row: 0, col: 5 },
        },
      },
    })
    this.newGame(1);
  }
  levels
  newGame = (level) => {
    fetch("http://stage.hinkhojdictionary.com:5001/v1/levels/?game_id=4").then((data) => {
      return data.json();
    }).then((levels) => {
      this.setState({ levels: levels.data.result })
      this.setState({ currentLevel: level })
      fetch("http://stage.hinkhojdictionary.com:5001/v1/crossword/?gl_id=" + level).then((data) => {
        return data.json();
      }).then((data) => {
        const gameData = data.data.result.word_lists.map((word) => { return { clue: word.hword, answer: word.eword, row: 0, col: 0 } })
        const across = [...gameData].splice(0, gameData.length / 2);
        const down = [...gameData].splice(gameData, gameData.length / 2, gameData.length - 1);
        this.setState({ gameData: { across, down } })
      })
    })
  }
  render() {
    return (
      <>
        <div style={{
          margin: 'auto', width: '50%', marginTop: "10px"
        }}>
          <ButtonToolbar aria-label="Toolbar with button groups">
            <ButtonGroup className="mr-2" aria-label="First group1">
              {this.state?.levels?.length > 0 && <Dropdown>
                <Dropdown.Toggle variant="success" id="dropdown-basic">
                  {"Level " + this.state.currentLevel}
                </Dropdown.Toggle>
                <Dropdown.Menu>
                  {this.state?.levels.map(level => {
                    return <Dropdown.Item key={level.level_id} onSelect={this.newGame.bind(this, level.level_id)}>
                      {level.level_name}
                    </Dropdown.Item>
                  })}
                </Dropdown.Menu>
              </Dropdown>
              }  </ButtonGroup>
            <ButtonGroup className="mr-2" aria-label="First group">
              <Button
                variant="outline-dark"
                onClick={this.newGame.bind(this, this.state?.currentLevel)}
              >
                New Game
                </Button>{" "}
            </ButtonGroup>
          </ButtonToolbar>
          {this.state?.gameData.across.length > 0 && <Crossword data={this.state?.gameData} useStorage={false}
            theme={{
              columnBreakpoint: '99px',
              gridBackground: '#acf',
              cellBackground: '#ffe',
              cellBorder: '#fca',
              textColor: '#fff',
              numberColor: '#9f9',
              focusBackground: '#f00',
              highlightBackground: '#f99',
            }} />}
        </div>
      </>
    );
  }

}

