import React, { Component } from "react";
import {
  Button,
  ButtonGroup,
  ButtonToolbar,
  Container,
  Dropdown,
  Jumbotron,
  Row,
} from "react-bootstrap";

export class ScrabbleComponent extends Component<{}, {}> {
  word: any;
  hint = "";
  display: any;
  selection: any;
  disabled: any;
  successJumbotron: any;
  failureJumbotron: any;
  hintJumbotron: any = "";
  failed = false;
  success = false;
  hintFlag = false;
  selectionIndex: any;
  level = "Level 1";
  constructor(props: any) {
    super(props);
    this.newGame();
  }
  shuffleArray(array: any) {
    array.sort(() => Math.random() - 0.5);
    return array;
  }
  showHint() {
    // this.hintFlag = true;
    // this.selection.push(this.word.charAt(this.selection.length));
    this.wordClick(
      this.word.charAt(this.selection.length),
      this.display.findIndex((e: any, index: any) => {
        if (this.disabled[index]) {
          return false;
        } else {
          return e === this.word.charAt(this.selection.length);
        }
      }),
      {}
    );
  }
  undo() {
    console.log(this);
    this.selection.pop();
    this.hintFlag = false;
    this.disabled[this.selectionIndex.pop()] = false;
    this.setState({});
  }
  check() {
    if (this.selection.join("") === this.word) {
      this.failed = false;
      this.success = true;
      this.successJumbotron = (
        <Jumbotron className="text-center">
          <h1>You Win</h1>
          <p>
            <i className="fa fa-smile-o fa-3x" aria-hidden="true"></i>
          </p>
          <p>
            Correct word &nbsp;
            {this.word}
          </p>
          <p>
            <Button variant="primary" onClick={this.newGame.bind(this)}>
              Next
            </Button>
          </p>
        </Jumbotron>
      );
    } else {
      this.failed = true;
      this.success = false;
      this.failureJumbotron = (
        <Jumbotron className="text-center">
          <h1>Try again!</h1>
          <p>
            <i className="fa fa-frown-o fa-3x" aria-hidden="true"></i>
          </p>
          <p>
            Correct word &nbsp;
            {this.word}
          </p>
          <p>
            <Button variant="primary" onClick={this.newGame.bind(this)}>
              Next
            </Button>
          </p>
        </Jumbotron>
      );
    }
    this.hintFlag = false;
    this.setState({});
  }

  wordClick(letter: any, clickIndex: any, event: any) {
    console.log(event);
    console.log(letter);
    this.selection.push(letter);
    this.selectionIndex.push(clickIndex);
    this.disabled[clickIndex] = true;
    if (this.disabled.flat().length === this.display.length) {
      this.hintFlag = true;
    }
    this.setState({});
  }
  newGame() {
    if (this.level === "Level 1") {
      this.word = "early";
      this.hint = "जल्दी";
    } else if (this.level === "Level 2") {
      this.word = "Squeeze";
      this.hint = "निचोड़ना";
    } else {
      this.word = "abnormal";
      this.hint = "असामान्य";
    }
    this.display = [...this.word];
    this.display = this.shuffleArray(this.display);
    this.selection = [];
    this.selectionIndex = [];
    this.disabled = [];
    this.failed = false;
    this.success = false;
    this.hintFlag = false;
    this.setState({});
  }
  changeLevel(level: any) {
    this.level = "Level " + level;
    this.newGame();
    this.setState({});
  }
  render() {
    return (
      <>
        <div style={{ marginBottom: "8px" }}></div>{" "}
        <Container className="justify-content-center">
          <Row className="justify-content-center"></Row>
          <div style={{ marginBottom: "8px" }}></div>{" "}
          <Row className="justify-content-center">
            <ButtonToolbar aria-label="Toolbar with button groups">
              <ButtonGroup className="mr-2" aria-label="First group1">
                <Dropdown>
                  <Dropdown.Toggle variant="success" id="dropdown-basic">
                    {this.level}
                  </Dropdown.Toggle>

                  <Dropdown.Menu>
                    <Dropdown.Item onSelect={this.changeLevel.bind(this, 1)}>
                      {" "}
                      Level 1
                    </Dropdown.Item>
                    <Dropdown.Item onSelect={this.changeLevel.bind(this, 2)}>
                      {" "}
                      Level 2
                    </Dropdown.Item>
                    <Dropdown.Item onSelect={this.changeLevel.bind(this, 3)}>
                      {" "}
                      Level 3
                    </Dropdown.Item>
                  </Dropdown.Menu>
                </Dropdown>
              </ButtonGroup>
              <ButtonGroup className="mr-2" aria-label="First group">
                <Button
                  variant="outline-dark"
                  onClick={this.newGame.bind(this)}
                >
                  New Game
                </Button>{" "}
              </ButtonGroup>
              <ButtonGroup className="mr-2" aria-label="First group">
                <Button
                  variant="outline-dark"
                  disabled={
                    this.selectionIndex.length === 0 ||
                    this.failed ||
                    this.success
                  }
                  onClick={this.undo.bind(this)}
                >
                  Undo
                </Button>
              </ButtonGroup>
              <ButtonGroup className="mr-2" aria-label="First group">
                <Button
                  disabled={this.hintFlag || this.failed || this.success}
                  variant="outline-dark"
                  onClick={this.showHint.bind(this)}
                >
                  Hint
                </Button>{" "}
              </ButtonGroup>
            </ButtonToolbar>
          </Row>
          <div style={{ marginBottom: "8px" }}></div>{" "}
          <Row className="justify-content-center">
            <Jumbotron className="text-center">
              <h1>Meaning</h1>
              <p>{this.hint}</p>
            </Jumbotron>
          </Row>{" "}
          <Row className="justify-content-center">
            <ButtonToolbar aria-label="Toolbar with button groups">
              {this.word.split("").map((leter: string, index: any) => {
                return (
                  <ButtonGroup
                    className="mr-2"
                    aria-label="First group"
                    key={"fill" + index}
                  >
                    <Button variant="outline-primary">
                      {this.selection[index]}
                    </Button>
                  </ButtonGroup>
                );
              })}
            </ButtonToolbar>
          </Row>
          <div style={{ marginBottom: "8px" }}></div>{" "}
          <Row className="justify-content-center">
            <ButtonToolbar aria-label="Toolbar with button groups">
              {this.display.map((letter: string, index: any) => {
                return (
                  <ButtonGroup
                    className="mr-2"
                    aria-label="First group"
                    key={"word" + index}
                  >
                    <Button
                      // hidden={this.disabled[index] === true}
                      onClick={this.wordClick.bind(this, letter, index)}
                      disabled={this.disabled[index] === true}
                    >
                      {letter}
                    </Button>
                  </ButtonGroup>
                );
              })}
            </ButtonToolbar>
          </Row>{" "}
          <div style={{ marginBottom: "8px" }}></div>
          <Row
            className="justify-content-center"
            hidden={this.selection.flat().length !== this.word.length}
          >
            <Button
              disabled={this.success || this.failed}
              onClick={this.check.bind(this)}
            >
              Check
            </Button>
          </Row>{" "}
          <div style={{ marginBottom: "8px" }}></div>
          <Row className="justify-content-center" hidden={!this.hintFlag}>
            {this.hintJumbotron}
          </Row>{" "}
          <Row
            className="justify-content-center"
            hidden={!(this.success && !this.failed)}
          >
            {this.successJumbotron}
          </Row>{" "}
          <Row
            className="justify-content-center"
            hidden={!(!this.success && this.failed)}
          >
            {this.failureJumbotron}
          </Row>{" "}
        </Container>
      </>
    );
  }
}
