import fetch from 'isomorphic-unfetch'

var LevelsService = {
   _getRequestHeaders: function() {
      return {}
   },

   getGameLevels: function(game_id, lang_id) {
      var headers = {}
      headers = LevelsService._getRequestHeaders()
      let url = `http://stage.hinkhojdictionary.com:5001/v1/levels/?game_id=${game_id}&lang_id=${lang_id}`
      return new Promise(function (resolve, reject) {
         fetch(url, { headers: headers })
            .then(function (response) {
               if (response.status >= 400) {
                  reject("Bad response from server");
               }
               return response.json();
            })
            .then(function (result) {
               var level_lists = []
               if (result.code === 200 && result.data && result.data.result) {
                  level_lists = result.data.result || []                  
               }
               resolve(level_lists)
            })
      })
   }
}
export default LevelsService