import React, { Component } from "react";
import {
  Button,
  ButtonGroup,
  ButtonToolbar,
  Container,
  Dropdown,
  Jumbotron,
  Row,
} from "react-bootstrap";
import LevelsService from './../../services/levels'
import ScrabbleService from './../../services/scrabble'


export class Scrabble extends Component {
  word = ""
  hint = "";
  display = []
  selection = []
  disabled = []
  successJumbotron = ""
  failureJumbotron = ""
  hintJumbotron = ""
  selectionIndex = []
  level = ""
	game_id = 1
  lang_id = 1
  gl_id = 0


	constructor(props) {
    	super(props);
    	this.state = {
        level_lists: [],
        hint: '',
        word: '',
        display: [],
        selection: [],
        disabled: [],
        selectionIndex: [],
        failed: false, 
        success: false, 
        hintFlag: false
      }
  }

	componentDidMount(){
    	this.loadGameLevels()
  }

	loadGameLevels = () => {
		this.fetchGameLevels().then((data) => {
		  this.setState({ level_lists: data })
      this.gl_id = data[0].gl_id
      this.changeLevel(data[0].level_name, this.gl_id, false)
      this.loadGameData()
		})
	}

	fetchGameLevels = async () => {
		try {
		  let levels = await LevelsService.getGameLevels(this.game_id, this.lang_id)
		  return levels
		} catch (err) {
		  console.log(err)
		}
	}

  loadGameData = () => {
    this.fetchGameData().then((data) => {
      console.log(data)
      this.word = data.eword;
      this.hint = data.hword;
      this.setState({hint: data.hword, word: data.eword})
      this.newGame()
    })
  }

  fetchGameData = async () => {
    try {
      let game_data = await ScrabbleService.getGameData(this.gl_id)
      return game_data
    } catch (err) { }
  }

	shuffleArray(array) {
		array.sort(() => Math.random() - 0.5);
		return array;
	}

	showHint() {
		// this.selection.push(this.word.charAt(this.selection.length));
		this.wordClick(
		  this.state.word.charAt(this.state.selection.length),
		  this.state.display.findIndex((e, index) => {
		    if (this.state.disabled[index]) {
		      return false;
		    } else {
		      return e === this.word.charAt(this.state.selection.length);
		    }
		  }),
		  {}
		);
	}

	undo() {
		this.selection.pop();
		this.disabled[this.selectionIndex.pop()] = false;

    this.setState({hintFlag: false})
    this.setState({selection: this.selection, disabled: this.disabled})    
	}

  check() {
    if (this.state.selection.join("") === this.state.word) {
      this.setState({failed: false, success: true})
      this.successJumbotron = (
        <Jumbotron className="text-center">
          <h1>You Win</h1>
          <p>
            <i className="fa fa-smile-o fa-3x" aria-hidden="true"></i>
          </p>
          <p>
            Correct word &nbsp;
            {this.state.word}
          </p>
          <p>
            <Button variant="primary" onClick={this.loadGameData.bind(this)}>
              Next
            </Button>
          </p>
        </Jumbotron>
      );
    } else {
      this.setState({failed: true, success: false})
      this.failureJumbotron = (
        <Jumbotron className="text-center">
          <h1>Try again!</h1>
          <p>
            <i className="fa fa-frown-o fa-3x" aria-hidden="true"></i>
          </p>
          <p>
            Correct word &nbsp;
            {this.state.word}
          </p>
          <p>
            <Button variant="primary" onClick={this.loadGameData.bind(this)}>
              Next
            </Button>
          </p>
        </Jumbotron>
      );
    }
    this.setState({hintFlag: false})
  }

	wordClick(letter, clickIndex, event) {
		console.log(event);
		console.log(letter);
		this.selection.push(letter);
		this.selectionIndex.push(clickIndex);
		this.disabled[clickIndex] = true;

    this.setState({selection: this.selection, selectionIndex: this.selectionIndex, disabled: this.disabled})
		if (this.disabled.flat().length === this.display.length) {
      this.setState({hintFlag: true})
		}
	}

	newGame() {
		this.display = [...this.word];
		this.display = this.shuffleArray(this.display);
		this.selection = [];
		this.selectionIndex = [];
		this.disabled = [];

    this.setState({display: this.display, selection: this.selection, selectionIndex: this.selectionIndex, disabled: this.disabled})
    this.setState({failed: false, success: false, hintFlag: false})
	}

	changeLevel(level, gl_id, load_new_game) {
    this.gl_id = gl_id
		this.level = level;
    if(load_new_game == true){
      this.loadGameData();
    }    
	}

  render() {      
	  var childMenuItem = this.state.level_lists.map((item) => {
      return (
          <Dropdown.Item onSelect={this.changeLevel.bind(this, item.level_name, item.gl_id, true)} key={item.gl_id}>
            {" "}
            {item.level_name}
          </Dropdown.Item>
      )
    })


    var suggestionLetterBox = this.state.display.map((letter, index) => {
      return (
          <ButtonGroup className="mr-2" aria-label="First group" key={"word" + index}>
            <Button onClick={this.wordClick.bind(this, letter, index)} disabled={this.state.disabled[index] === true}>
              {letter}
            </Button>
          </ButtonGroup>
      )
    })


    var inputFillLetterBox = this.state.word.split("").map((letter, index) => {
      return (
          <ButtonGroup className="mr-2" aria-label="First group" key={"fill" + index}>
            <Button variant="outline-primary">
              {this.state.selection[index]}
            </Button>
          </ButtonGroup>
      )
    })
          
    return (      
      <>
        <div style={{ marginBottom: "8px" }}></div>{" "}
        <Container className="justify-content-center">
          <Row className="justify-content-center"></Row>
          <div style={{ marginBottom: "8px" }}></div>{" "}
          <Row className="justify-content-center">
            <ButtonToolbar aria-label="Toolbar with button groups">
              <ButtonGroup className="mr-2" aria-label="First group1">
                <Dropdown>
                  <Dropdown.Toggle variant="success" id="dropdown-basic">
                    {this.level}
                  </Dropdown.Toggle>

                  <Dropdown.Menu>
                    {childMenuItem}
                  </Dropdown.Menu>
                  
                </Dropdown>
              </ButtonGroup>
              <ButtonGroup className="mr-2" aria-label="First group">
                <Button
                  variant="outline-dark"
                  onClick={this.loadGameData.bind(this)}
                >
                  New Game
                </Button>{" "}
              </ButtonGroup>
              <ButtonGroup className="mr-2" aria-label="First group">
                <Button
                  variant="outline-dark"
                  disabled={
                    this.state.selectionIndex.length === 0 ||
                    this.state.failed ||
                    this.state.success
                  }
                  onClick={this.undo.bind(this)}
                >
                  Undo
                </Button>
              </ButtonGroup>
              <ButtonGroup className="mr-2" aria-label="First group">
                <Button
                  disabled={this.state.hintFlag || this.state.failed || this.state.success}
                  variant="outline-dark"
                  onClick={this.showHint.bind(this)}
                >
                  Hint
                </Button>{" "}
              </ButtonGroup>
            </ButtonToolbar>
          </Row>

          <div style={{ marginBottom: "8px" }}></div>{" "}
          <Row className="justify-content-center">
            <Jumbotron className="text-center">
              <h1>Meaning</h1>
              <p>{this.state.hint}</p>
            </Jumbotron>
          </Row>{" "}


          <Row className="justify-content-center">
            <ButtonToolbar aria-label="Toolbar with button groups">
              {inputFillLetterBox}              
            </ButtonToolbar>
          </Row>

          <div style={{ marginBottom: "8px" }}></div>{" "}
          <Row className="justify-content-center">
            <ButtonToolbar aria-label="Toolbar with button groups">
              {suggestionLetterBox}
            </ButtonToolbar>
          </Row>{" "}
          <div style={{ marginBottom: "8px" }}></div>


          <Row
            className="justify-content-center"
            hidden={this.state.selection.flat().length !== this.state.word.length}
          >
            <Button
              disabled={this.state.success || this.state.failed}
              onClick={this.check.bind(this)}
            >
              Check
            </Button>
          </Row>{" "}


          <div style={{ marginBottom: "8px" }}></div>
          <Row className="justify-content-center" hidden={!this.state.hintFlag}>
            {this.hintJumbotron}
          </Row>{" "}
          <Row
            className="justify-content-center"
            hidden={!(this.state.success && !this.state.failed)}
          >
            {this.successJumbotron}
          </Row>{" "}
          <Row
            className="justify-content-center"
            hidden={!(!this.state.success && this.state.failed)}
          >
            {this.failureJumbotron}
          </Row>{" "}
        </Container>
      </>
    );
  }
}