import fetch from 'isomorphic-unfetch'

var ScrabbleService = {
  _getRequestHeaders: function() {
     return {}
  },

  getGameData: function(gl_id) {
    var headers = {}
    headers = ScrabbleService._getRequestHeaders()
    let url = `http://stage.hinkhojdictionary.com:5001/v1/scrabble/?gl_id=${gl_id}`
    return new Promise(function (resolve, reject) {
      fetch(url, { headers: headers })
        .then(function (response) {
          if (response.status >= 400) {
            reject("Bad response from server");
          }
          return response.json();
        })
        .then(function (result) {
          var game_data = {}
          if (result.code === 200 && result.data && result.data.result) {
            game_data = result.data.result || {}
          }
          resolve(game_data)
        })
    });
  },
  getWordSearchLevelsByGameId: function () {
    var headers = {};
    headers = ScrabbleService._getRequestHeaders();
    let url = `http://stage.hinkhojdictionary.com:5001/v1/levels/?game_id=3`;
    return new Promise(function (resolve, reject) {
      fetch(url, { headers: headers })
        .then(function (response) {
          if (response.status >= 400) {
            reject("Bad response from server");
          }
          return response.json();
        })
        .then(function (result) {
          var game_data = {};
          if (result.code === 200 && result.data && result.data.result) {
            game_data = result.data.result || {};
          }
          resolve(game_data);
        });
    });
  },
  getWordSearchDataByLevel: function (gl_id) {
    var headers = {};
    headers = ScrabbleService._getRequestHeaders();
    let url = `http://stage.hinkhojdictionary.com:5001/v1/wordsearch/?gqid=${gl_id}`;
    return new Promise(function (resolve, reject) {
      fetch(url, { headers: headers })
        .then(function (response) {
          if (response.status >= 400) {
            reject("Bad response from server");
          }
          return response.json();
        })
        .then(function (result) {
          var game_data = {};
          if (result.code === 200 && result.data && result.data.result) {
            game_data = result.data.result || {};
          }
          resolve(game_data);
        });
    });
  },
};
export default ScrabbleService;
