import React, { Component } from 'react';
import Speech from 'speak-tts'
import speaker from '../../assets/img/spell_bee_speaker.png';
import {
    Button,
    ButtonGroup,
    ButtonToolbar,
    Col,
    Container,
    Jumbotron,
    Form,
    Row,
    Image
} from "react-bootstrap";
import './SpellBee.css'

export class SpellBeeGame extends Component {



    constructor(props) {

        super(props);

        this.state = {
            meaning: "word",
            hindiMeaning: "Hindi",
            hint: "w***",
            enteredWord: "",
            isAnswerSubmitted: false,
            isCorrectAnswer: false,
            isNewGameClicked: true,
            hintLen: 1
        };
    }

    checkSupport = () => {
        var speech = new Speech()
        if (speech.hasBrowserSupport()) { // returns a boolean
            console.log("speech synthesis supported")

            speech.init(
                {
                    'volume': 1,
                    'lang': 'en-IN',
                    'rate': 1,
                    'pitch': 1,
                    'voice': 'Google IN English Female',
                    'splitSentences': true,
                    'listeners': {
                        'onvoiceschanged': (voices) => {
                            console.log("Event voiceschanged", voices)
                        }
                    }
                }
            ).then((data) => {
                // The "data" object contains the list of available voices and the voice synthesis params
                console.log("Speech is ready, voices are available", data)
            }).catch(e => {
                console.error("An error occured while initializing : ", e)
            })

        }
        // will throw an exception if not browser supported

    }


    speakWord = (word) => {
        console.log("speakWord clicked")
        var speech = new Speech()

        if (speech.hasBrowserSupport()) { // returns a boolean
            speech.speak({
                text: word,
            }).then(() => {
                console.log("Success !")
            }).catch(e => {
                console.error("An error occurred :", e)
            })
        }


    }

    checkWordSpelling = () => {

        if (this.state.meaning === this.state.enteredWord) {
            this.setState({ isNewGameClicked: false, isCorrectAnswer: true, isAnswerSubmitted: true })
            console.log("speakWord correct")
        } else {
            this.setState({ isNewGameClicked: false, isCorrectAnswer: false, isAnswerSubmitted: true })
            this.setState({ failed: false, success: true })
        }
    }


    loadNewGame = () => {
        this.setState({
            meaning: "word",
            hindiMeaning: "Hindi1",
            hint: "w" + "ord".replace(/./g, '*'),
            enteredWord: "",
            isAnswerSubmitted: false,
            isCorrectAnswer: false,
            isNewGameClicked: true,
            hintLen: 1
        });
    }

    showHint = () => {
        var hintLen = this.state.hintLen + 1;
        let newHint = "";

        for (let index = 0; index < this.state.meaning.length; index++) {
            if (index > hintLen - 1) {
                newHint = newHint + "*";
            } else {
                newHint = newHint + this.state.meaning[index];
            }
        }

        this.setState({
            hint: newHint,
            hintLen: hintLen
        });

    }


    render() {

        const isNewGameClicked = this.state.isNewGameClicked;

        let result;
        if (isNewGameClicked) {
            result = <h1></h1>;
        } else {
            if (this.state.isAnswerSubmitted && this.state.isCorrectAnswer) {
                result =
                    <Jumbotron className="text-center">
                        <h1>You Win</h1>
                        <p>
                            <i className="fa fa-smile-o fa-3x" aria-hidden="true"></i>
                        </p>
                        <p>
                            Correct word &nbsp;
            {this.state.word}
                        </p>
                        <p>
                            <Button variant="primary" onClick={this.loadNewGame.bind(this)}>
                                Next
            </Button>
                        </p>
                    </Jumbotron>

            } else {
                result =
                    <Jumbotron className="text-center">
                        <h1>Try again!</h1>
                        <p>
                            <i className="fa fa-frown-o fa-3x" aria-hidden="true"></i>
                        </p>
                        <p>
                            Correct word &nbsp;
            {this.state.word}
                        </p>
                        <p>
                            <Button variant="primary" onClick={this.loadNewGame.bind(this)}>
                                Next
            </Button>
                        </p>
                    </Jumbotron>

            }

        }
        return (
            <Container >
                <Row className="justify-content-center">
                    <Col><Button onClick={this.showHint.bind(this)}>Hint</Button></Col>
                    <Col >
                        <Image src={speaker} onClick={this.speakWord.bind(this, this.state.meaning)} roundedCircle />
                    </Col>
                    <Col> <Button onClick={this.loadNewGame.bind(this)}>New Game</Button></Col>
                </Row>

                <hr class="dashed"></hr>

                <Row xs={2} md={4} lg={6}>
                    <Col>Meaning</Col>
                    <Col>{this.state.meaning}</Col>
                </Row>

                <Row xs={2} md={4} lg={6}>
                    <Col>Hindi Meaning</Col>
                    <Col>{this.state.hindiMeaning}</Col>
                </Row>
                <Row xs={2} md={4} lg={6}>
                    <Col>{this.state.hint}</Col>
                </Row>

                <Form>
                    <Form.Group controlId="formBasicEmail">
                        <Form.Control placeholder="Type your word here" value={this.state.enteredWord}
                            onChange={e => this.setState({ enteredWord: e.target.value })} />

                    </Form.Group>

                    <Button variant="primary" type="submit" onClick={this.checkWordSpelling.bind(this)}>Check </Button>

                </Form>

                {result}

            </Container>

        );
    }

}

